          </v-flex>

            <v-flex xs12 sm12 md12 lg12 xl12>
              <div class="porcentajes">
                <v-flex xs12 sm12 md12 lg12 xl12 v-if="act">
                  <table>
                    <tr>
                      <td>
                        <span class="green--text" style="font-size: 15px"
                          >Total diezmos</span
                        >
                      </td>
                      <td>${{totaldiezmos}}</td>                                                      
                    </tr> 
                    <tr>
                      <td>
                        <span class="green--text" style="font-size: 15px"
                          >3%</span
                        >
                      </td>
                      <td>${{tresporciento}}</td>
                    </tr>
                    <tr>
                      <td>
                        <span class="green--text" style="font-size: 15px"
                          >Diezmos - 3%</span
                        >
                      </td>
                      <td>${{diezmomenosporcentaje}}</td>
                    </tr>
                     <tr>
                      <td>
                        <span class="green--text" style="font-size: 15px"
                          >Total  ofrendas</span
                        >
                      </td>
                      <td>${{totalofrendas}}</td>
                    </tr>
                     <tr>
                      <td>
                        <span class="green--text" style="font-size: 15px"
                          >Ofrendas + (diezmos-3%)</span
                        >
                      </td>
                      <td>${{ofrendasmenosdiezmos}}</td>
                    </tr>
                    <tr>
                      <td>
                        <span class="green--text" style="font-size: 15px"
                          >10%</span
                        >
                      </td>
                      <td>${{diezporciento}}</td>
                    </tr>
                  </table>
                </v-flex>
              </div>
            </v-flex>

          <v-flex xs12 sm12 md12 lg12 xl12>
