              @click.native="mostrarmodalguardar()"
              >Guardar</v-btn
            >
          </v-flex>
          </div>
        </v-layout>
      </v-container>
      <v-container grid-list-sm class="pa-4 white" v-if="verRegistro">
        <span class="" style="font-size: 30px">Ver registro de porcentaje:</span>
       <div><br/></div>
       <div><br/></div>
       <div><br/></div>
       <div><br/></div>

        <div id="factura" ref="test">
            <header>
